import React from 'react'
import { MdRadioButtonUnchecked, MdRadioButtonChecked, MdKeyboardArrowLeft } from 'react-icons/md';
export default class User extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            topNavSelected: 'shops',
            shoppingList: [
                {
                    description: 'mayo',
                    item: {
                        name: 'mayonaise', 
                        brand: 'no name', 
                        shopname: 'Pick n Pay', 
                        amount: 50
                    }
                },
                {
                    description: 'mayo',
                    item: {
                        name: 'mayonaise', 
                        brand: 'no name', 
                        shopname: 'Pick n Pay', 
                        amount: 50
                    }
                },

                {
                    description: 'mayo',
                    item: null
                },

            ],
            openNavItem: -1,

            items: [],

            shops: [],

            resultsIndex: -1,
        };
    }

    componentDidMount() {
        this.test()
    }

	render()	{

        return (
            <div style={{position: 'absolute', height: '100%', width: '100%', background: '#eeeeee', display: 'flex', flexDirection: 'column', color: '#7B7B7B'}}>
                {/*Top nav bar */}
                <div style={{width: '100%', height: '70px', background: '#FFF96D', boxShadow: '2px 2px 10px #BCBCBC', display: 'flex', alignItems: 'center'}}>
                    <div onClick={() => this.setSelectedNav('shops')} style={{flexGrow: this.state.topNavSelected === 'shops' ? 1 : 0, height: '100%', transition: 'all 0.3s ease', minWidth: '20%', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Shopping
                    </div>

                    <div style={{height: 'calc(100% - 10px)', width: '2px', background: 'white'}}></div>

                    <div onClick={() => this.setSelectedNav('accounts')} style={{flexGrow: this.state.topNavSelected === 'accounts' ? 1 : 0, minWidth: '20%', transition: 'all 0.3s ease', height: '100%', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Account
                    </div>

                    <div style={{height: 'calc(100% - 10px)', width: '2px', background: 'white'}}></div>

                    <div onClick={() => this.setSelectedNav('cart')} style={{flexGrow: this.state.topNavSelected === 'cart' ? 1 : 0, minWidth: '20%', height: '100%', transition: 'all 0.3s ease', display: 'flex', justifyContent: 'center', alignItems: 'center', cursor: 'pointer', fontSize: '30px', color: "#7B7B7B"}}>
                        Cart
                    </div>
                    
                </div>

                <div style={{flexGrow: 1, width: '100%', display: 'flex', }}>
                    <div style={{height: '100%', width: '300px', maxWidth: '30%', background: 'white', }}>
                        {this.state.shoppingList.map((prop, key) =>
                            <div onClick={() => this.setOpenNavItem(key)} style={{cursor: 'pointer', height: this.state.openNavItem === key ? '80px' : '60px', width: '100%', transition: 'all 0.2s ease', overflow: 'hidden'}}>
                                <div style={{width: '100%', height: '40px', display: 'flex'}}>
                                    <div style={{height: '100%', width: '40px', display: 'flex', justifyContent: 'center', alignItems: 'center', fontSize: '30px'}}>{prop.item ? <MdRadioButtonChecked /> : <MdRadioButtonUnchecked />}</div>
                                    <input onChange={(e) => this.setListItem(e, key)} value={prop.description} style={{border: 'none', borderBottom: '2px solid #7B7B7B', outline: 'none', background: 'transparent'}} />
                                </div>

                                <div style={{height: '40px', width: '100%', background: 'green'}}>
                                </div>
                            </div>
                        )}

                    </div>

                    {
                        this.state.resultsIndex !== -1 ?
                            <div style={{height: '100%', width: 'calc(100% - 300px)', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                    <div style={{height: 'calc(100% - 40px)', width: 'calc(100% - 40px)', background: 'white', boxShadow: '2px 2px 10px #BCBCBC'}}>
                                        <div style={{width: '100%', height: '80px', display: 'flex', alignItems: 'center'}}>
                                            <div onClick={this.exitResults} style={{height: '100%', width: '80px', display: 'flex', justifyContent: 'center', alignItems: 'center', fontSize: '30px', color: "#7B7B7B", cursor: 'pointer'}}><MdKeyboardArrowLeft /></div>
                                            showing results for:
                                            <div style={{marginLeft: '10px', fontSize: '25px'}}>{this.state.shoppingList[this.state.resultsIndex].description}</div>
                                        </div>
                                    </div>
                            </div>
                            :
                            <div style={{height: '100%', width: 'calc(100% - 300px)', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                                <div style={{height: 'calc(100% - 40px)', width: 'calc(100% - 40px)', background: 'white', boxShadow: '2px 2px 10px #BCBCBC'}}>
                                    <div style={{width: '100%', height: '80px',  display: 'flex', alignItems: 'center', justifyContent: 'center', fontSize: '25px', fontWeight: 'bold'}}>
                                        Shops around you
                                    </div>
                                    <div style={{width: '100%', display: 'flex', height: 'calc(100% - 80px)', overflowY: 'auto', overflowX: "hidden", flexWrap: 'wrap'}}>
                                        {
                                            this.state.shops.map((prop, key) =>
                                                <div style={{display: 'flex', margin: '10px', height: '200px', width: '120px', background: '#eeeeee', justifyContent: 'center', alignItems: 'center'}}>
                                                    {prop}
                                                </div>
                                            )
                                        }
                                    </div>
                                </div>
                            </div>
                    }
                </div>
            </div>
        )
    }

    exitResults = () => {
        this.setState({resultsIndex: -1})
    }

    setListItem = (e, index) => {
        var shoppingList = this.state.shoppingList
        console.log(index)
        shoppingList[index].description = e.target.value
        this.setState({shoppingList, resultsIndex: index})
    }

    setOpenNavItem = (openNavItem) => {
        if(openNavItem === this.state.openNavItem)
            this.setState({openNavItem: -1})
        else 
            this.setState({openNavItem})

    }

    setSelectedNav = (topNavSelected) => {
        this.setState({topNavSelected})
    }

    test = async () => {
        const response = await fetch(this.props.url+'/api/items');
        console.log(response)
        const data = await response.json();
        console.log(data)
        var shops = []
        
        for(var i = 0; i < data.length; i++) {
            if(shops.indexOf(data[i].shopname) === -1)
                shops.push(data[i].shopname)
        }

        console.log(shops)

        this.setState({items: data, shops: shops})
    }
}

